import React, { Component } from 'react'
import '../CssFolder/pleasewait.css'
import { Button, Dialog, DialogTitle, DialogContent, DialogActions } from 'react-mdl'

class PleaseWaitDialog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isOpen: true
        };
        this.handleOpenDialog = this.handleOpenDialog.bind(this);
        this.handleCloseDialog = this.handleCloseDialog.bind(this);

        
      }
     
    handleOpenDialog() {
        this.setState({
          isOpen: true
        });
      }
    
      handleCloseDialog() {
        this.setState({
          isOpen: false,
        });
      }


    render() {
        let dialog = (
            <div>
        
            <Dialog  style={{ borderRadius:"15px", boxShadow:'0px 0px 20px'}} open={this.state.isOpen} onCancel={this.handleCloseDialog}>

               
            <img style={{marginBottom:'-5px', objectFit:'contain'}} className="loadingGif" src="https://thumbs.gfycat.com/UnitedSmartBinturong-size_restricted.gif"/>
            <h5 style={{marginBottom:'-5px'}}>Loading..</h5>
              
                {/* <p style={{color:'black', marginBottom:'-5px', textAlign:'start'}}>Loading..</p> */}
       

            </Dialog>
          </div>
          );

          if(!this.props.isOpen)
          {
              dialog =null;
          }
        return (

           
        <div>
            {dialog}
        </div>
        )
    }
}

export default PleaseWaitDialog
