import axios from 'axios'
import '../CssFolder/selectedcat.css'

import React, { Component } from 'react'
import { useStateValue } from './StateProvider';
import PleaseWaitDialog from './PleaseWaitDialog';

class SelectedCategory extends Component {

    constructor(props) {
        super(props)

        this.state = {
            posts: [],
            errorMsg: '',
            isOpen:'true'
        }

    }

    addToBasket = (id) => {
    }

    componentDidMount() {
        axios.get('https://script.googleusercontent.com/macros/echo?user_content_key=k2NgaE3F1mtV7pT79opMO_G5Xx_j2YUknZiInyiKHHX_EC4zs9UvJVY354s-uZDuBSkmk2AexDafGm1P4HQFdPAz4NBNRTmOOJmA1Yb3SEsKFZqtv3DaNYcMrmhZHmUMWojr9NvTBuBLhyHCd5hHa1GhPSVukpSQTydEwAEXFXgt_wltjJcH3XHUaaPC1fv5o9XyvOto09QuWI89K6KjOu0SP2F-BdwUv0Xv6biqdsHBluyJzEAGn_K0nQR14pe2E-1k2WEcgr8rXkF-XNUExIuTJNFCMGM95y7FLqOV0Tk27B8Rh4QJTQ&lib=MnrE7b2I2PjfH799VodkCPiQjIVyBAxva')
            .then(response => {
                console.log(response)
                
                this.setState({
                    isOpen:false,
                    posts: response.data.Sheet1
                })
                
            })
            .catch(error => {
                console.log(error)
                this.setState({
                    errorMsg: 'Error retriving data'

                })
            })
    }


    render() {
        const { posts, errorMsg } = this.state
        return (

            <div>
                <PleaseWaitDialog isOpen={this.state.isOpen}/>
            

            <div className="scmain">
                
                {
                    posts.length ?
                        posts.map(posts => (localStorage.getItem("cat") == posts.cat) ? (

                            <div className="selectedcat_MainDiv" key={posts.name}>
                                <img className="selectedcat_img" src={posts.imgUrl} />
                                <div className="nameDiv">
                                    <p className="selectedcat_name">{posts.name}</p>
                                </div>

                                <p className="selectedcat_kg">{posts.kgValue}</p>

                                <div className="selectedcat_AddBtnDiv">
                                    <p className="selectedcat_pricTxt">Price : {posts.price}</p>
                                    <button id="addBtn" onClick={() => {
                                        this.addToBasket(posts.name)
                                    }} className="selectedcatBtn">Add</button>
                                </div>
                            </div>
                        ) : (null)


                        ) : null}
                        
            </div>
            </div>

        )
    }
}

export default SelectedCategory
