import React from 'react'
import 'react-slideshow-image/dist/styles.css'
import { Slide } from 'react-slideshow-image';
import '../CssFolder/imageslider2.css'
 

function ImageSlider2() {

    const slideImages = [
        'https://www.bigbasket.com/media/uploads/banner_images/All_Bakery_DT_1_1130x400_25thJuly.jpg',
        'https://www.bigbasket.com/media/uploads/banner_images/All_PetStore_DT_2_1130x400_25thJuly.jpg',
        'https://www.bigbasket.com/media/uploads/banner_images/All_BeautyWFH_DT_4_1130x400_4thAug.jpg'
      ];

      return (
        <div className="slide-container2">
          <Slide transitionDuration="500" indicators={true}>
            <div className="each-slide2">
              <div className="s_img2" style={{'backgroundImage': `url(${slideImages[0]})`}}>
                <span></span>
              </div>
            </div>
            <div className="each-slide2">
              <div className="s_img2" style={{'backgroundImage': `url(${slideImages[1]})`}}>
                <span></span>
              </div>
            </div>
            <div className="each-slide2">
              <div className="s_img2" style={{'backgroundImage': `url(${slideImages[2]})`}}>
                <span></span>
              </div>
            </div>
          </Slide>
        </div>
      )
}

export default ImageSlider2
