import React from 'react'
import '../CssFolder/categories.css'
import { useStateValue } from './StateProvider';

function Categories({ id, title, imgUrl, weight, price }) {

    const [{ basket }, dispatch] = useStateValue();

    var addTxt = "Add";



    const addToBasket = (id) => {
        var toAdd = true;

        console.log(id);
        {
            basket.map(item => {
                console.log(item)
                    if(id === item.id){
                        toAdd = false
                    }
                    else{
                        // alert("false")
                    }
                }
            )
        }

        if (toAdd) {

            dispatch({
                type: 'ADD_TO_BASKET',
                item: {
                    id: id,
                    title: title,
                    price: price,
                    imgUrl: imgUrl,
                    weight: weight
                }
            })
        }
    }

    const changeTxt = (button) => {

    }



    return (

        <div className="cat_MainDiv" >
            <img className="cat_img" src={imgUrl} />
            <div className="nameDiv">
                <p className="cat_name">{title}</p>
            </div>

            <p className="cat_kg">{weight}</p>

            <div className="cat_AddBtnDiv">
                <p className="cat_pricTxt">Price : {price}</p>
                <button id="addBtn" onClick={() => {
                    addToBasket(id)
                }} className="catBtn">{addTxt}</button>
            </div>
        </div>
    )
}


export default Categories
