import React from 'react'
import 'react-slideshow-image/dist/styles.css'
import { Slide } from 'react-slideshow-image';
import '../CssFolder/imageslider.css'
 

function ImageSlider() {

    const slideImages = [
        'https://www.bigbasket.com/media/customPage/355c27b8-a44f-4900-a390-8e82c69b8021/3ba01bcf-87ea-4405-b15a-73a0a8ab9da9/f49e42cf-347d-49e9-b832-5d76866b9461/T1_All_Handwash-Sanitizers_560x378_25thJuly.jpg',
        'https://www.bigbasket.com/media/customPage/355c27b8-a44f-4900-a390-8e82c69b8021/3ba01bcf-87ea-4405-b15a-73a0a8ab9da9/a5d53bd7-5f8a-463b-bd4c-cf4b520d9f2a/T1_All_Organic-Store_560x378_25thJuly.jpg',
        'https://www.bigbasket.com/media/customPage/355c27b8-a44f-4900-a390-8e82c69b8021/3ba01bcf-87ea-4405-b15a-73a0a8ab9da9/e01f46f3-6822-4360-810c-8805de650f59/2008055_frozen-snacks_378.jpg'
      ];

      return (
        <div className="slide-container">
          <Slide transitionDuration="500" indicators={true}>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[0]})`}}>
                <span>Handwash and sanitizers</span>
              </div>
            </div>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[1]})`}}>
                <span>Organic store</span>
              </div>
            </div>
            <div className="each-slide">
              <div className="s_img" style={{'backgroundImage': `url(${slideImages[2]})`}}>
                <span>Frozen Snacks</span>
              </div>
            </div>
          </Slide>
        </div>
      )
}

export default ImageSlider
