import React from 'react'
import '../CssFolder/cat.css'
import { Link } from 'react-router-dom'
import { useStateValue } from './StateProvider';

function Cat({imgUrl, cat}) {

   

    const routeToSelectedCat =()=>{
        localStorage.setItem("cat",cat);
    }

    return (
        <Link onClick={routeToSelectedCat} to={process.env.PUBLIC_URL +"/products"}>
        <div className="cat_MainDiv">
            <img src={imgUrl}/>
        </div>
        </Link>
    )
}

export default Cat
